    .panel .login {
      height: 45px;
      width: 100%;
      background-color: #8bc34a;
      -webkit-border-radius: 45px;
         -moz-border-radius: 45px;
              border-radius: 45px;
      position: relative;
      line-height: 45px;
      text-align: center;
      font-weight: bold;
      color: white;
      margin-top: 10px;
      -webkit-transition: background .2s;
         -moz-transition: background .2s;
           -o-transition: background .2s;
              transition: background .2s;
      cursor: pointer;
    }
    .panel .login:active {
      -webkit-transform: translateY(2px);
         -moz-transform: translateY(2px);
          -ms-transform: translateY(2px);
           -o-transform: translateY(2px);
              transform: translateY(2px);
    }
    .panel .login:hover {
      background-color: #599b2d;
    }
    .panel .login:after {
      content: "\f084";
      font-family: 'FontAwesome';
      position: absolute;
      width: 45px;
      height: 45px;
      background-color: #599b2d;
      color: #fff;
      text-shadow: 1px -1px #467a23, 2px -2px #487d24, 3px -3px #4a8025, 4px -4px #4b8326, 5px -5px #4d8627, 6px -6px #4f8928, 7px -7px #508c28, 8px -8px #528f29, 9px -9px #54922a, 10px -10px #55952b, 11px -11px #57982c;
      left: 0;
      top: 0;
      -webkit-transform: rotate(90deg);
         -moz-transform: rotate(90deg);
          -ms-transform: rotate(90deg);
           -o-transform: rotate(90deg);
              transform: rotate(90deg);
      -webkit-border-radius: 50%;
         -moz-border-radius: 50%;
              border-radius: 50%;
      text-align: center;
      line-height: 45px;
    }
    .panel input[type='text'] {
      background-color: #22232a;
